<template>
  <div id="page-container">
    <!-- Main Container -->
    <main id="main-container">
      <div class="bg-image">
        <div class="row no-gutters justify-content-center bg-primary-dark-op">
          <div class="hero-static col-sm-8 col-md-6 col-xl-4 d-flex align-items-center p-2 px-sm-0">
            <div class="block block-transparent block-rounded w-100 mb-0 overflow-hidden">
              <div class="block-content block-content-full px-lg-5 px-xl-6 py-4 py-md-5 py-lg-6 bg-white">
                <div class="mb-2 text-center">
                  <a class="link-fx font-w700 font-size-h1" href="#">
                    <span class="text-dark">ShopFBPro</span><span class="text-primary">.COM</span>
                  </a>
                  <p class="text-uppercase font-w700 font-size-sm text-muted">Tạo tài khoản</p>
                </div>
                <form class="js-validation-signup" v-on:submit.prevent="signup()">

                  <div class="form-group">
                    <div class="input-group">
                      <input type="number" v-model="phone_number"
                             class="form-control"
                             placeholder="Số điện thoại" name="phone_number" required>
                      <div class="input-group-append">
                                            <span class="input-group-text">
                                                <i class="fa fa-user-circle"></i>
                                            </span>
                      </div>
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="input-group">
                      <input type="password" v-model="password"
                             class="form-control"
                             placeholder="Mật khẩu"
                             name="password" required>
                      <div class="input-group-append">
                            <span class="input-group-text">
                                <i class="fa fa-asterisk"></i>
                            </span>
                      </div>
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="input-group">
                      <input type="text" v-model="name" minlength="4" maxlength="30"
                             class="form-control"
                             placeholder="Họ và tên"
                             name="name" required>
                      <div class="input-group-append">
                            <span class="input-group-text">
                                <i class="fa fa-envelope-open"></i>
                            </span>
                      </div>
                    </div>
                  </div>
                  <div class="form-group text-center">
                    <button type="submit" name="submit" class="btn btn-hero-primary"><i
                      class="fa fa-fw fa-user mr-1"></i>Đăng ký
                    </button>
                  </div>
                </form>
              </div>
              <div class="block-content bg-body">
                <div class="d-flex justify-content-center text-center push">
                  <div class="font-w600 font-size-sm py-1 text-center">
                    Đã có tài khoản? <router-link to="dang-nhap"> Đăng nhập</router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import swal from 'sweetalert'
export default {
  name: "signup",
  data() {
    return {
      phone_number: null,
      password: undefined,
      name: null,
      uid_facebook: null,
    }
  },
  methods: {
    signup() {
      this.$auth.register({
        data: {
          phone_number: this.phone_number,
          password: this.password,
          name: this.name,
          uid_facebook: this.uid_facebook
        },
      }).then((response) => {
        console.log(response)
        swal("Đăng ký thành công","","success")
        setTimeout(() => {
          window.location.href = "/"
        }, 2000)
      }, (reason) => {
        let status = reason.response.status;
        if (status >= 400 && status <= 499) {
          swal("Đăng ký không thành công", "Tài khoản đã tồn tại", "warning");
        } else {
          swal("Đăng ký không thành công", "Lỗi hệ thống", "warning");
        }
      })
    }
  }

}
</script>

