<template>
  <div class="container-movie-list">
    <div class="d-flex justify-content-between container-section">
      <label class="lable-section">{{ listTitle }}</label>
      <label class="lable-section-btn">See All ></label>
    </div>
    <div class="row">
      <div class="col-lg-12">
        <div class="top-movie-list block-wrapper">
          <div class="list_carousel">
            <div class="caroufredsel_wrapper">
              <VueSlickCarousel
                  v-bind="slideSetting"
              >
                <ItemFilm :title="'Nevertheless 1'"/>
                <ItemFilm :title="'Nevertheless 2'"/>
                <ItemFilm :title="'Nevertheless 3'"/>
                <ItemFilm :title="'Nevertheless 4'"/>
                <ItemFilm :title="'Nevertheless 5'"/>
                <ItemFilm :title="'Nevertheless 6'"/>
                <ItemFilm :title="'Nevertheless 7'"/>
                <ItemFilm :title="'Nevertheless 8'"/>
                <img
                    slot="prevArrow"
                    style="float: left"
                    src="@/assets/prev-slide.png"
                    alt="prev slide"
                />
                <img
                    slot="nextArrow"
                    src="@/assets/next-slide.png"
                    alt="next slide"
                />
              </VueSlickCarousel>
              <div class="line"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ItemFilm from "@/views/components/ItemFilm";
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
// optional style for arrows & dots
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";

export default {
  name: "MovieList",
  components: {ItemFilm, VueSlickCarousel},
  props: {
    listTitle: String,
  },
  data() {
    return {
      slideSetting: {
        arrows: true,
        centerMode: true,
        centerPadding: '50',
        slidesToShow: 5,
        slidesToScroll: 5,
        responsive: [
          {
            breakpoint: 1200,
            settings: {
              centerMode: true,
              slidesToShow: 4,
              slidesToScroll: 4,
            },
          },
          {
            breakpoint: 992,
            settings: {
              centerMode: true,
              slidesToShow: 3,
              slidesToScroll: 3,
            },
          },
          {
            breakpoint: 768,
            settings: {
              centerMode: true,
              slidesToShow: 3,
              slidesToScroll: 3,
            },
          },
          {
            breakpoint: 576,
            settings: {
              centerMode: true,
              slidesToShow: 2,
              slidesToScroll: 2,
            },
          },
          {
            breakpoint: 480,
            settings: {
              centerMode: true,
              centerPadding: '30',
              slidesToShow: 2,
              slidesToScroll: 2,
            },
          },
        ],
      }
    };
  },
};
</script>

<style lang="scss" scoped>
.container-movie-list {
  margin-bottom: 30px;
}

.container-section {
  padding-bottom: 4px;
}

.lable-section {
  font-size: 16px;
  font-weight: bold;
  color: #39c2ff;
  text-transform: uppercase;
}

.lable-section-btn {
  margin-right: 18px;
  font-size: 14px;
  font-weight: bold;
  color: #ffffff;
}

.top-movie-list {
  padding: 15px;
}

.block-wrapper {
  position: relative;
  padding: 2px;
  // border-bottom: 1px solid #868489;
  color: #ddd;
  height: auto;
  display: block;
}

.top-movie-list.block-wrapper > .list_carousel {
  overflow: hidden;
}

.list_carousel {
  width: 100%;
  position: relative;
}

.top-movie-list .caroufredsel_wrapper {
  white-space: nowrap;
}

.caroufredsel_wrapper {
  width: 100% !important;
  display: block;
  text-align: start;
  float: none;
  position: relative;
  inset: auto;
  z-index: auto;
  padding-top: 10px;
  padding-bottom: 15px;
  padding-right: 10px;
  margin: 0px;
  overflow: hidden;

  .slick-prev,
  .slick-next {
    width: 36px;
    height: 36px;
    top: 40%;
    z-index: 30;
  }

  .slick-prev {
    left: 0;
  }

  .slick-next {
    right: 12px;
  }
}

.movie-carousel-top {
  white-space: nowrap;
}

.list_carousel ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: block;
}

.movie-carousel-top {
  text-align: left;
  float: none;
  position: absolute;
  inset: 0px auto auto 0px;
  margin: 0px;
  width: 10406px;
}

.line {
  width: 99.8%;
  height: 1px;
  background: #868489;
  margin-top: 30px;
}
</style>
