<template>
  <div id="page-container">
    <!--  Main content-->
    <main id="main-container">
      <div class="bg-image">
        <div class="row no-gutters justify-content-center bg-primary-dark-op">
          <div class="hero-static col-sm-8 col-md-6 col-xl-4 d-flex align-items-center p-2 px-sm-0">
            <div class="block block-transparent block-rounded w-100 mb-0 overflow-hidden">
              <div class="block-content block-content-full px-lg-5 px-xl-6 py-4 py-md-5 py-lg-6 bg-white">
                <div class="mb-2 text-center">
                  <a class="link-fx font-w700 font-size-h1" href="#">
                    <span class="text-dark">ShopFBPro</span><span class="text-primary">.COM</span>
                  </a>
                  <p class="text-uppercase font-w700 font-size-sm text-muted">Đăng nhập</p>
                  <i id="error_login" style="color: red"></i>
                </div>

                <form class="js-validation-signin" v-on:submit.prevent="login()">
                  <div class="form-group">
                    <div class="input-group">
                      <input id="phone_number" v-model="phone_number"
                             class="form-control"
                             name="phone_number"
                             placeholder="Số điện thoại" required
                             style="-moz-appearance: textfield;" type="number">
                      <div class="input-group-append">
                          <span class="input-group-text">
                              <i class="fa fa-user-circle"></i>
                          </span>
                      </div>

                    </div>
                  </div>
                  <div class="form-group">
                    <div class="input-group">
                      <input id="password" v-model="password"
                             class="form-control"
                             name="password" placeholder="Mật khẩu"
                             required type="password">
                      <div class="input-group-append">
                                            <span class="input-group-text">
                                                <i class="fa fa-asterisk"></i>
                                            </span>
                      </div>
                    </div>
                  </div>
                  <div class="form-group text-center">
                    <button class="btn btn-hero-primary" name="submit" type="submit"><i
                      class="fa fa-fw fa-sign-in-alt mr-1"></i> Đăng nhập
                    </button>
                  </div>
                </form>
              </div>
              <div class="block-content bg-body">
                <div class="d-flex justify-content-center text-center push">
                  <div class="font-w600 font-size-sm py-1 text-center">
                    Chưa có tài khoản? <router-link to="dang-ky">Đăng ký</router-link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>

    <!--  END OF main content-->
  </div>
</template>

<script>
import swal from 'sweetalert'
export default {
  name: "login",
  data() {
    return {
      phone_number: null,
      password: undefined,
    }
  },
  mounted() {
  },
  methods: {
    login() {
      this.$auth.login({
        data: {
          phone_number: this.phone_number,
          password: this.password
        },
      }).then((response) => {
        console.log(response)
        swal("Đăng nhập thành công", "", "success");
      }, (reason) => {
        console.log(reason)
        swal("Đăng nhập thất bại", "Tài khoản hoặc mật khẩu không chính xác", "warning")
      })
    }
  }
}
</script>
