<template>
  <div>
    <VueSlickCarousel
        :dots="true"
        :arrows="true"
        :slidesToShow="1"
        :slidesToScroll="1"
    >
      <SlideItem/>
      <SlideItem/>
      <SlideItem/>
      <SlideItem/>
      <SlideItem/>
      <SlideItem/>
      <SlideItem/>
      <img slot="prevArrow" class="arrow" style="float: left" src="@/assets/slide-bar-prev.png" alt="prev slide">
      <img slot="nextArrow" class="arrow" src="@/assets/slide-bar-next.png" alt="next slide">
    </VueSlickCarousel>
  </div>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
import SlideItem from "@/views/components/SlideItem";

export default {
  name: "SliderBar",
  components: {SlideItem, VueSlickCarousel},
  data() {
    return {
      slide: 0,
      sliding: null
    }
  },
  methods: {
    onSlideStart() {
      this.sliding = true
    },
    onSlideEnd() {
      this.sliding = false
    }
  }
}
</script>
<style lang="scss">
@media only screen and (min-width: 768px) {
  .slick-dots {
    bottom: 20px;

    li {
      button:before {
        color: white;
        opacity: 64;
        font-size: 10px;
        //font-size: 2em;
      }
    }

    li.slick-active {
      button:before {
        color: #39C2FF;
        font-size: 16px;
        //font-size: 2em;
      }
    }
  }
  .slick-prev, .slick-next {
    width: 50px;
    height: 50px;
    top: 50%;
    z-index: 5;
  }
  .slick-prev {
    left: 30px;
  }
  .slick-next {
    right: 30px;
  }
}
</style>
<style lang="scss">
@media only screen and (max-width: 768px) {
  .arrow {
    display: none;
  }
  .slick-dots {
    bottom: 130px;
    li {
      margin: 0;
      button:before {
        color: white;
        opacity: 64;
        font-size: 5px;
        //font-size: 2em;
      }
    }
    li.slick-active {
      button:before {
        color: #39C2FF;
        font-size: 8px;
        //font-size: 2em;
      }
    }
  }
}
</style>
