    <div class="row menu text-menu col">
        <ul class="menu text-menu">
          <li id="anime" class="text-menu" @mousedown="showMenuItem('anime')">Anime</li>
          <li id="movies" class="text-menu" @mousedown="showMenuItem('movies')">Movies</li>
          <li id="r_show" class="text-menu" @mousedown="showMenuItem('r_show')">Reality show</li>
