<script>
import { mixin as clickaway } from 'vue-clickaway';
export default {
  mixins: [ clickaway ],
  name: "Header",
  mounted () {
    this.hideSubmenu();
  },
  data() {
    return {
      isShowSubMenu: false
    };
  },
methods: {
    resetClassMenu(){
      let anime = document.getElementById("anime");
      let movies = document.getElementById("movies");
      let r_show = document.getElementById("r_show");
      let tv_show = document.getElementById("tv_show");

      anime.className = ""
      movies.className = ""
      r_show.className = ""
      tv_show.className = ""
    },
    showMenuItem(id) {
      let menu_item = document.getElementById(id);
      if (menu_item.className === "") {
        this.showSubmenu()
        menu_item.className = "active"
      } else {
        this.hideSubmenu()
      }
    },
    showSubmenu(){
      this.isShowSubMenu = true
      this.resetClassMenu()
      let submenu = document.getElementById("menu-item");
      submenu.className = "showing-on";
      submenu.visible = false
    },
    hideSubmenu() {
      this.isShowSubMenu = false
      let submenu = document.getElementById("menu-item");
      submenu.className = "showing-off";
      this.resetClassMenu()
      submenu.visible = false
    },
  },
};
</script>

