import bearer from '@websanova/vue-auth/drivers/auth/bearer';
import axios from '@websanova/vue-auth/drivers/http/axios.1.x';
import router from '@websanova/vue-auth/drivers/router/vue-router.2.x';

const config = {
  auth: bearer,
  http: axios,
  router: router,
  tokenDefaultName: 'auth-token',
  tokenStore: ['cookie'],
  notFoundRedirect: {
    path: '/'
  },
  registerData: {
    url: '/api/auth/signup',
    method: 'POST',
    redirect: '/login',
  },
  loginData: {
    url: '/api/auth/login',
    method: 'POST',
    redirect: '/',
    fetchUser: true,
  },
  logoutData: {
    url: '/api/auth/logout',
    method: 'POST',
    redirect: '/dang-xuat',
    makeRequest: true
  },
  fetchData: {
    url: '/api/auth/user',
    method: 'GET',
    enabled: true
  },
  parseUserData (data) {
    return data || {}
  },
  rolesKey: 'role'
};

export default config;
